
html,
body {
  background-color: #f0f0f0;
  color: #333;
  font-size: 0.33333333rem;
}
* {
  margin: 0;
  padding: 0;
  outline: 0;
}
ul {
  margin: 0;
  padding: 0;
}
ul li {
  list-style: none;
}
.webkit-box {
  display: -ms-flexbox;
  display: flex;
}
.center-box {
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.cartcontrol .cart-decrease .minus-icon {
  background: url("../../assets/minus.png") no-repeat;
  background-size: 0.69444444rem 0.69444444rem;
  width: 0.69444444rem;
  height: 0.69444444rem;
  display: inline-block;
}
.cartcontrol .cart-decrease.move-enter-active,
.cartcontrol .cart-decrease.move-leave-active {
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease.move-enter,
.cartcontrol .cart-decrease.move-leave-active {
  opacity: 0;
  transform: translate3d(0.66666667rem, 0, 0);
}
.cartcontrol .cart-decrease.move-enter .inner,
.cartcontrol .cart-decrease.move-leave-active .inner {
  transform: rotate(180deg);
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 0.44444444rem;
  text-align: center;
  padding-top: 0.27777778rem;
  line-height: 0.27777778rem;
  font-size: 0.44444444rem;
  color: #333;
}
.cartcontrol .cart-add {
  display: inline-block;
  background: url("../../assets/plus.png") no-repeat;
  background-size: 0.69444444rem 0.69444444rem;
  width: 0.69444444rem;
  height: 0.69444444rem;
}



